.container {
    width: 50px;
    height: 50px;

    border: 2px dashed var(--secondary-blue-pale-15);
    border-radius: 15px;

    box-shadow: 0 4px 4px 0 var(--shadow-transparent);
    background-color: transparent;

    overflow: hidden;
}

.image {
    width: 100%;
    height: 100%;
}
